@import url(https://fonts.googleapis.com/css?family=Fira+Code&display=swap);
@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #bd93f9;
  opacity: 0.1;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

.input-container {
  display: flex;
  position: relative;
  flex-direction: column;
  padding: 15px;
}

#editor {
  outline: none;
  resize: none;
  height: 90%;
  padding: 25px 20px;
  border-radius: 8px;
  overflow: auto;

  background-color: #282a36;
  color: #fff;
  line-height: 1.4em;
  font-weight: normal;
  font-family: 'Fira Code', monospace;
}

#editor:focus {
  border-color: #ff79c6;
}

#clear-btn {
  display: none;
  outline: none;
  position: fixed;
  left: 0.5%;
  top: 89%;
  align-self: center;
  width: 75px;

  color: #fff;
  font-size: 16px;
  font-weight: bold;
  background: none;
  border: none;
  text-transform: uppercase;
  opacity: 0.1;
}

#editor:focus + #clear-btn {
  display: block;
}

#clear-btn:hover {
  display: block;
  opacity: 0.5;
}

.preview-container {
  padding: 15px;
}

#preview {
  height: 90%;
  padding: 25px 20px;
  width: 93%;
  resize: none;
  outline: none;
  overflow: auto;

  border-width: 1px;
  border-style: solid;
  border-color: rgb(169, 169, 169);
  -webkit-border-image: none;
          border-image: none;
  -webkit-border-image: initial;
          border-image: initial;
  border-radius: 8px;
  background-color: #282a36;
  color: #fff;
}

#preview:focus {
  border-color: #ff79c6;
}

.App {
  display: grid;
  grid-template-columns: 42.5% 15% 42.5%;

  min-height: 100vh;
  width: 100vw;

  background-image: -webkit-gradient(linear, left top, right bottom, from(#44475a), to(#6272a4));

  background-image: linear-gradient(to bottom right, #44475a, #6272a4);
}

#menu-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

#app-icon {
  opacity: 0.75;
  margin-top: 15px;
}

#company-icon {
  color: #fff;
  opacity: 0.75;
  font-family: 'Pacifico', cursive;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0;
  margin-bottom: 15px;
}

#confirm-button {
  width: 100%;
  background: #303341;
  cursor: pointer;
  border: 1px solid #ff93d1;

  outline: none;
  border-radius: 5px;
  padding: 15px;

  -webkit-transition: all 0.4s;

  transition: all 0.4s;
}

#confirm-button:hover {
  background: #282a36;
  border: 1px solid #ff7fc8;
}

